* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

.App {
  text-align: center;
}

nav {
  display: flex;
  justify-content: space-around;
  align-items: center;
  min-height: 10vh;
  background-color: rgb(73, 79, 82);
  color: #fff;
}

.page {
  width: 80%;
  max-width: 1000px;
  margin: 0 auto;
}

.nav-links {
  width: 50%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  list-style: none;
}

.nav-links a {
  text-decoration: none;
  text-transform: uppercase;
}

.page-header {
  padding: 50px 70px;
}

.grid {
  display: flex;
  flex-wrap: wrap;
  padding: 0;
}

.grid::after {
  content: "";
  width: 100%;
  margin: 0.5rem;
  margin-bottom: 1rem;
}

.card {
  border: 1px solid #ccc;
  box-shadow: 2px 4px 2px #ccc;
  width: 100%;
  min-height: 250px;
  margin: 1rem;
  text-decoration: none;
  transition: all 0.2s;
}

.card:hover {
  box-shadow: 4px 8px 4px #ccc;
  padding: 0;
}

@media (min-width: 551px) {
  .card {
    width: calc(45% - 0.5rem);
  }
}
@media (min-width: 551px) {
  .grid::after {
    width: calc(45% - 0.5rem);
  }
}

@media (min-width: 801px) {
  .card {
    width: calc(30% - 0.5rem);
  }
}

@media (min-width: 801px) {
  .grid::after {
    width: calc(30% - 0.5rem);
  }
}

.card-title-container {
  display: flex;
  justify-content: center;
  height: 50px;
  padding: 10px;
  overflow: hidden;
  background-color: #fbce11;
}

.card-title {
  color: #d04e02;
  line-height: 35px;
  text-transform: capitalize;
}

.card-body {
  padding: 15px;
  text-align: left;
}

@media (min-width: 551px) {
  .article {
    display: flex;
    flex-direction: column;
  }
}

.article {
  margin: 20px 0;
  flex-direction: row;
}

.article-thumbnail img {
  max-width: 300px;
}

@media (min-width: 551px) {
  .article-thumbnail img {
    min-width: 125px;
    width: 125px;
    margin-top: 7.5px;
  }
}

.article-text {
  padding-left: 20px;
  text-align: left;
}

.page a {
  font-weight: 300;
  color: #777;
  text-decoration: none;
}

.search-form {
  min-height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 20px 0 50px;
}

.search-bar {
  width: 80%;
  border: none;
  border-bottom: 1px solid #111;
  padding: 10px;
  font-size: 20px;
  outline: none;
}

.search-button {
  background-color: #b23a48;
  border: none;
  padding: 10px 20px;
  color: #fff;
  font-size: 20px;
}

.search-button:hover {
  background: #8c2f39;
}

.search-button:focus,
.search-button:active {
  background-color: #461220;
}

.newsletter-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 20px auto;
  background-color: #ccc;
  width: 300px;
  font-size: 16px;
  padding: 50px 30px;
}

.newsletter-form input {
  padding: 10px;
  border: 1px solid #ccc;
  outline-color: #b23a48;
}

.newsletter-form input::placeholder {
  font-size: 12px;
  font-weight: 300;
}

.newsletter-form button {
  padding: 10px;
  background-color: #b23a48;
  border: 1px solid #b23a48;
  outline: none;
  padding: 10px 20px;
  color: #fff;
}

.newsletter-form input,
.newsletter-form button {
  width: 60%;
}

.additional-info {
  height: 50px;
  font-size: 12px;
  color: #d00;
  padding: 10px 0;
}

.label {
  font-weight: 500;
}
